@import"https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600;700&display=swap";:root{--primary-color: #f77301;--text-color: #ffffff;--bg-color: #000000;--secondary-bg: #111111}body{margin:0;padding:0;background-color:var(--bg-color);color:var(--text-color);font-family:"Montserrat",Arial,sans-serif}.app{min-height:100vh;display:flex;flex-direction:column}.loader{position:fixed;top:0;left:0;width:100%;height:100%;background:var(--bg-color);display:flex;justify-content:center;align-items:center;z-index:9999}.loader-logo{font-size:48px;font-weight:700;color:var(--primary-color)}.ink-cursor{pointer-events:none;position:fixed;display:block;border-radius:0;transform-origin:center center;mix-blend-mode:difference;top:0;left:0;z-index:9999999}.ink-cursor span{position:absolute;display:block;width:25px;height:25px;border-radius:20px;background-color:var(--primary-color);transform-origin:center center;transform:translate(-50%, -50%);transition:width .2s,height .2s}@media(max-width: 991px){.ink-cursor{display:none}}.vector-menu-content{position:fixed;top:0;left:0;width:100%;z-index:1000;padding:20px 0;background-color:rgba(0,0,0,.9);backdrop-filter:blur(10px)}.vector-menu-content .nav-content{display:flex;justify-content:space-between;align-items:center}.vector-menu-content .logo{font-size:24px;font-weight:700;color:var(--primary-color)}.vector-menu-content-list{list-style:none;padding:0;margin:0;display:flex;gap:30px}.vector-menu-content-item a{color:var(--text-color);text-decoration:none;font-size:16px;transition:color .3s ease;position:relative}.vector-menu-content-item a:after{content:"";position:absolute;width:0;height:2px;bottom:-5px;left:0;background-color:var(--primary-color);transition:width .3s ease}.vector-menu-content-item a:hover{color:var(--primary-color)}.vector-menu-content-item a:hover:after{width:100%}.hero{min-height:100vh;display:flex;align-items:center;position:relative;overflow:hidden}.hero .animated-background{position:absolute;top:0;left:0;width:100%;height:100%;background:linear-gradient(45deg, #000 25%, #111 25%, #111 50%, #000 50%, #000 75%, #111 75%, #111 100%);background-size:20px 20px;animation:moveBackground 20s linear infinite;opacity:.1}.hero .hero-content{position:relative;z-index:1}.hero .marketing-text{font-size:96px;line-height:1.1;margin-bottom:30px;font-weight:700}@media(max-width: 1199px){.hero .marketing-text{font-size:72px}}@media(max-width: 959px){.hero .marketing-text{font-size:54px}}@media(max-width: 639px){.hero .marketing-text{font-size:44px}}.hero .marketing-text em{font-style:normal;color:var(--primary-color)}.hero .marketing-text .dot{color:var(--primary-color)}.hero .hero-description{font-size:24px;margin-bottom:40px;opacity:.8;max-width:600px}.hero .hero-buttons{display:flex;gap:20px}.hero .hero-buttons .t-btn.secondary{background-color:rgba(0,0,0,0);border:2px solid var(--primary-color)}.hero .hero-buttons .t-btn.secondary:hover{background-color:var(--primary-color)}.about-section{padding:100px 0;background-color:var(--secondary-bg)}.about-section .about-content{display:grid;grid-template-columns:1fr 1fr;gap:50px;align-items:center}@media(max-width: 768px){.about-section .about-content{grid-template-columns:1fr}}.about-section .about-image .avatar-container{width:300px;height:300px;margin:0 auto;position:relative}.about-section .about-image .avatar-container .avatar-animation{width:100%;height:100%;border-radius:50%;background:linear-gradient(45deg, var(--primary-color), #ff9f4a);animation:pulse 2s ease-in-out infinite}.about-section .about-text h2{font-size:48px;margin-bottom:30px;color:var(--primary-color)}.about-section .about-text p{font-size:18px;line-height:1.6;margin-bottom:30px;opacity:.8}.about-section .about-text .skills-list{display:flex;flex-wrap:wrap;gap:15px;list-style:none;padding:0}.about-section .about-text .skills-list li{padding:10px 20px;background:rgba(247,115,1,.1);border-radius:20px;color:var(--primary-color);cursor:pointer}.portfolio-section{padding:100px 0}.portfolio-section .section-title{font-size:48px;margin-bottom:40px;text-align:center;color:var(--primary-color)}.portfolio-section .portfolio-filters{display:flex;justify-content:center;gap:15px;margin-bottom:40px}.portfolio-section .portfolio-filters .filter-btn{padding:10px 20px;background:rgba(0,0,0,0);border:2px solid var(--primary-color);color:var(--text-color);border-radius:20px;cursor:pointer;transition:all .3s ease}.portfolio-section .portfolio-filters .filter-btn.active,.portfolio-section .portfolio-filters .filter-btn:hover{background:var(--primary-color)}.portfolio-section .portfolio-grid{display:grid;grid-template-columns:repeat(auto-fit, minmax(300px, 1fr));gap:30px}.portfolio-section .portfolio-item{background:var(--secondary-bg);border-radius:10px;overflow:hidden}.portfolio-section .portfolio-item .portfolio-image{height:200px;background:#222;position:relative}.portfolio-section .portfolio-item .portfolio-image .swedev-watermark{position:absolute;bottom:10px;right:10px;font-size:12px;color:hsla(0,0%,100%,.3)}.portfolio-section .portfolio-item h3{padding:20px;margin:0;font-size:20px;color:var(--primary-color)}.portfolio-section .portfolio-item p{padding:0 20px 20px;margin:0;opacity:.8}.services-section{padding:100px 0;background-color:var(--secondary-bg)}.services-section .section-title{font-size:48px;margin-bottom:60px;text-align:center;color:var(--primary-color)}.services-section .services-grid{display:grid;grid-template-columns:repeat(auto-fit, minmax(300px, 1fr));gap:30px}.services-section .service-card{background:var(--bg-color);padding:30px;border-radius:10px;border:1px solid rgba(247,115,1,.1)}.services-section .service-card .service-icon{width:60px;height:60px;margin-bottom:20px;background:rgba(247,115,1,.1);border-radius:10px}.services-section .service-card h3{font-size:24px;margin-bottom:10px;color:var(--primary-color)}.services-section .service-card .price{font-size:20px;margin-bottom:20px;color:var(--text-color)}.services-section .service-card .features-list{list-style:none;padding:0;margin-bottom:30px}.services-section .service-card .features-list li{margin-bottom:10px;opacity:.8}.services-section .service-card .features-list li:before{content:"•";color:var(--primary-color);margin-right:10px}.contact-section{padding:100px 0}.contact-section .section-title{font-size:48px;margin-bottom:60px;text-align:center;color:var(--primary-color)}.contact-section .contact-form{max-width:600px;margin:0 auto}.contact-section .contact-form .form-group{margin-bottom:20px}.contact-section .contact-form .form-group input,.contact-section .contact-form .form-group textarea{width:100%;padding:15px;background:var(--secondary-bg);border:1px solid hsla(0,0%,100%,.1);border-radius:5px;color:var(--text-color);font-size:16px;transition:all .3s ease}.contact-section .contact-form .form-group input:focus,.contact-section .contact-form .form-group textarea:focus{outline:none;border-color:var(--primary-color);box-shadow:0 0 0 2px rgba(247,115,1,.2)}.contact-section .contact-form .form-group textarea{height:150px;resize:vertical}.t-btn{display:inline-block;padding:15px 30px;background-color:var(--primary-color);color:var(--text-color);text-transform:uppercase;border:none;border-radius:4px;cursor:pointer;transition:all .3s ease;font-size:16px;font-weight:500;letter-spacing:1px}.t-btn:hover{transform:translateY(-2px);box-shadow:0 5px 15px rgba(247,115,1,.3)}.footer{background-color:var(--secondary-bg);padding:30px 0;margin-top:auto}.footer__content{display:flex;justify-content:space-between;align-items:center}@media(max-width: 768px){.footer__content{flex-direction:column;gap:20px;text-align:center}}.footer__logo{font-size:24px;font-weight:700;color:var(--primary-color)}.footer__social{display:flex;gap:20px}.footer__social a{color:var(--text-color);font-size:24px;transition:all .3s ease}.footer__social a:hover{color:var(--primary-color)}@keyframes moveBackground{0%{background-position:0 0}100%{background-position:40px 40px}}@keyframes pulse{0%{transform:scale(1);opacity:1}50%{transform:scale(1.05);opacity:.8}100%{transform:scale(1);opacity:1}}